.canvas-wrap {
    width: calc(100% - 300px);
    height: 100%;
    box-sizing: border-box;
    border: 1px solid #aaa;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
        height: 100px;
    }
    canvas {
        border: 1px solid #aaa;
    }
}
